import React, { useEffect, useState } from 'react'
import { Table, Form, Modal } from 'react-bootstrap';
import { useDispatch, useSelector } from 'react-redux';
import { UpdateUserInfo } from '../../../actions/auth';
import callApi from '../../../helpers/conectorApi';
import { alert_exitoso } from '../../../helpers/Notificacion';
import { NoAutorizado } from '../base/NoAutorizado';
import Loading from '../base/Loading';
const menuId = 37;
const menuIdSucursal = 33;
export const UsuarioSucursal = ({ usuarioId, abrirModal, setAbrirModal, catSucursales }) => {
    const state = useSelector(state => state);
    const [loading, setLoading] = useState(true)
    const dispatch = useDispatch();
    const [accesos, setAccesos] = useState([]);
    const [catSucursalesAsignadas, setCatSucursalesAsignadas] = useState([]);

    const GetAccesosByMenuId = () => {
        if (state?.accesos) {
            const { accesos } = state;
            const misAccesos = accesos.filter(item => item.menuId === menuId || item.menuId === menuIdSucursal);
            setAccesos(misAccesos);
        }
        setLoading(false);
    }

    const NuevoRegistro = async (data) => {
        let response = await callApi('usuario/sucursal', {
            method: 'POST',
            body: JSON.stringify(data)
        });

        if (response) {
            alert_exitoso("Sucursal Asignada exitosamente");
            GetSucursalesAsignadas(usuarioId);
        }
    }
    const ActualizarRegistro = async (data) => {
        let response = await callApi(`usuario/sucursal/${data.usuario_sucursalId}`, {
            method: 'PUT',
            body: JSON.stringify(data)
        });

        if (response) {
            alert_exitoso(response);
            GetSucursalesAsignadas(usuarioId);
        }
    }

    const handleChangeChecbox = async (usuario_sucursalId, sucursalId, estadoId) => {
        setLoading(true);
        if (usuario_sucursalId === 0) {
            const data = {
                usuarioId,
                sucursalId,
                estadoId: 1
            };

            await NuevoRegistro(data);
        } else if (usuario_sucursalId > 0) {
            let estadoAux = 3;
            if (estadoId === 1) {
                estadoAux = 2;
            } else if (estadoId === 2) {
                estadoAux = 1;
            }
            const data = {
                usuario_sucursalId,
                estadoId: estadoAux
            };
            await ActualizarRegistro(data);
        }
        setLoading(false);
    }
    const GetSucursalesAsignadas = async (id) => {
        if (accesos.find(acceso => acceso.menuId === menuId && acceso.accesoId === 3)) {
            setLoading(true);
            dispatch(UpdateUserInfo());
            let response = await callApi(`usuario/sucursal?usuarioId=${id}&estadoId=1;2`);
            if (response) {
                setCatSucursalesAsignadas(response);
            }
            setLoading(false);
        }
    }

    useEffect(() => {
        GetAccesosByMenuId();
    }, []);

    useEffect(() => {
        GetSucursalesAsignadas(usuarioId);
    }, [usuarioId, accesos]);

    return (
        <Modal show={abrirModal} onHide={() => setAbrirModal(false)} size="lg">
            {
                loading === true ?
                    <Loading />
                    : <>
                        <Modal.Header closeButton>
                            <Modal.Title as="h5">Sucursales</Modal.Title>
                        </Modal.Header>
                        <Modal.Body>
                            {
                                accesos.find(acceso => acceso.menuId === menuId && acceso.accesoId === 3) ?
                                    accesos.find(acceso => acceso.menuId === menuIdSucursal && acceso.accesoId === 3) ?
                                        <Table striped hover responsive bordered id="mytable">
                                            <thead>
                                                <tr>
                                                    <th>Codigo Asignación</th>
                                                    <th>Acceso</th>
                                                    {
                                                        (accesos.find(acceso => acceso.menuId === menuId && acceso.accesoId === 2) || accesos.find(acceso => acceso.menuId === menuId && acceso.accesoId === 1)) &&
                                                        <th></th>
                                                    }
                                                </tr>

                                            </thead>
                                            <tbody>
                                                {
                                                    catSucursales.map(({ sucursalId, nombre }) => {
                                                        const filaRol = catSucursalesAsignadas.find(item => item.sucursalId === sucursalId);
                                                        let asignado = false;
                                                        const { usuario_sucursalId = 0, estadoId = 0 } = !!filaRol && filaRol;
                                                        if (estadoId === 1) {
                                                            asignado = true;
                                                        }
                                                        return (
                                                            (accesos.find(acceso => acceso.menuId === menuId && acceso.accesoId === 1) || asignado || usuario_sucursalId > 0) &&
                                                            <tr key={sucursalId}>
                                                                <td>{sucursalId}</td>
                                                                <td>{nombre}</td>
                                                                {
                                                                    (estadoId === 1 || estadoId === 2) ?
                                                                        <>{
                                                                            accesos.find(acceso => acceso.menuId === menuId && acceso.accesoId === 2) ?
                                                                                <td style={{ textAlign: "center" }}>
                                                                                    <Form.Group>
                                                                                        <div className="switch switch-alternative d-inline m-r-10">
                                                                                            <Form.Control type="checkbox" id={`rolId_${sucursalId}`} checked={asignado} onChange={() => { handleChangeChecbox(usuario_sucursalId, sucursalId, estadoId); }} />
                                                                                            <Form.Label htmlFor={`rolId_${sucursalId}`} className="cr" />
                                                                                        </div>
                                                                                        <Form.Label htmlFor={`rolId_${sucursalId}`}>{
                                                                                            asignado ? 'Activo' : 'Inactivo'
                                                                                        }</Form.Label>
                                                                                    </Form.Group>
                                                                                </td>
                                                                                :
                                                                                accesos.find(acceso => acceso.menuId === menuId && acceso.accesoId === 1) &&
                                                                                <td style={{ textAlign: "center" }}><label className="text-danger">No Autorizado</label></td>
                                                                        }
                                                                        </>
                                                                        :
                                                                        <>
                                                                            {
                                                                                accesos.find(acceso => acceso.menuId === menuId && acceso.accesoId === 1) ?
                                                                                    <td style={{ textAlign: "center" }}>
                                                                                        <Form.Group>
                                                                                            <div className="switch switch-alternative d-inline m-r-10">
                                                                                                <Form.Control type="checkbox" id={`rolId_${sucursalId}`} checked={asignado} onChange={() => { handleChangeChecbox(usuario_sucursalId, sucursalId, estadoId); }} />
                                                                                                <Form.Label htmlFor={`rolId_${sucursalId}`} className="cr" />
                                                                                            </div>
                                                                                            <Form.Label htmlFor={`rolId_${sucursalId}`}>Asignar Sucursal</Form.Label>
                                                                                        </Form.Group>
                                                                                    </td> : <td></td>
                                                                            }
                                                                        </>
                                                                }
                                                            </tr>
                                                        )
                                                    })
                                                }

                                            </tbody>
                                        </Table>
                                        : <NoAutorizado />
                                    : <NoAutorizado />
                            }
                        </Modal.Body>
                    </>
            }
        </Modal>
    )
}
